*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
ul,
li {
  list-style: none;
}
body {
  font-family: sans-serif;
}
.todo-wrapper {
  width: 500px;
  background-color: #555;
  margin: 20px auto;
  padding: 10px;
  border-radius: 5px;
}
.todo-wrapper h1 {
  text-align: center;
  letter-spacing: 1px;
  color: #fff;
  margin-bottom: 10px;
}
.todo-wrapper form input {
  width: 100%;
  padding: 7px;
  height: 40px;
}

.todo-wrapper ul {
  margin: 7px;
  background-color: #bbb;
  color: purple;
}
.todo-wrapper ul li {
  padding: 10px;
  text-transform: capitalize;
  display: flex;
  cursor: pointer;
  letter-spacing: 0.5px;
}
.todo-wrapper ul li:not(:last-child) {
  border-bottom: 1px solid #000;
}
.todo-wrapper ul li i:nth-child(1) {
  margin-left: auto;
  margin-right: 10px;
}
.todo-complete {
  background-color: green;
  color: #fff;
}
@media (max-width: 600px) {
  .todo-wrapper {
    width: 350px;
  }
  .todo-wrapper h1 {
    font-size: 1.4rem;
  }
}
